.chatscreen-header {
  align-items: center;
}
.chatscreen-header .logo img {
  width: 60px;
}
.right_wrapper {
  display: flex;
  align-items: center;
}
.right_wrapper .user_info {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.right_wrapper img {
  width: 45px;
  border-radius: 100%;
  border: 2px solid #ebebeb;
}
.right_wrapper .username {
  color: #ebebeb;
  font-size: 14px;
  margin: 0px 20px 0px 7px;
}
.right_wrapper .dropdown-toggle {
  width: 45px;
  height: 45px;
  display: flex;
  border-radius: 100%;
  align-items: center;
  justify-content: center;
  background: white;
  border: none;
  color: darkblue;
}
.right_wrapper .dropdown-toggle:focus {
  background: darkblue;
  color: darkblue;
}
.right_wrapper .dropdown-toggle:after {
  display: none;
}

.InformationCircleOutline {
  font-size: 25px;
  color: darkblue;
}
.formInput__group {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.forminput {
  border-radius: 50px !important;
  border: 2px solid var(--gray) !important;
  background: #ebebeb;
  height: 50px !important;
  width: 80%;
}
.submitButton {
  width: 43px;
  height: 43px !important;
  border-radius: 100px !important;
  position: relative;
  right: 48px;
}

.message {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.message img {
  width: 50px;
  border-radius: 100px;
  height: 50px;
  border: 2px solid darkblue;
}
.message .message_info {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
}
.message .message_info .time {
  position: relative;
  top: 7px;
  margin-left: 8px;
}
.message .message_info .time span {
  color: darkblue;
  font-size: 12px;
}
.message .username {
  margin: 0px;
  font-size: 14px;
  margin-bottom: 7px;
  color:darkblue
}
.message .messageText {
  margin: 0px;
  font-size: 13px;
  font-weight: 400;
  background: darkblue;
  color: white;
  padding: 9px;
  width: auto;
  text-align: left;
}
.messages_body {
  padding: 0px;
  overflow-y: scroll;
  margin-bottom: 14px;
}
.messages_body .chat-screen-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.messages_body .chat-screen-text p {
  font-size: 20px;
}
.messages_body .chat-screen-text p span {
  color: darkblue;
}
.messages_body::-webkit-scrollbar {
  width: 8px;
}
.messages_body::-webkit-scrollbar-thumb {
  background: darkblue;
  border-radius: 15px;
}
.messages_body::-webkit-scrollbar-track {
  background: darkblue;
}

.form-wrapper .forminput {
  border: 2px solid darkblue !important;
  background-color: lightblue;
}
.form-wrapper .submitButton {
  background: darkblue;
}

.form-wrapper .submitButton:hover {
  background: blue;
}

